.box {
  max-width: 1440px;
  margin: 96px auto;
  padding: 0 40px;
  display: flex;
  gap: 95px;
  align-items: center;
  justify-content: space-between;
}
.box .box__img {
  flex: 50% 1 1;
}
.box .box__img img {
  width: 100%;
  object-fit: contain;
}
.box .box__content {
  flex: 50% 1 1;
  color: #121212;
}
.box .box__content .box__title {
  font-weight: 700;
  font-size: 42px;
  margin-bottom: 24px;
}
.box .box__content .box__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-bottom: 32px;
}
.box .box__content .box__button {
  border: none;
  background-color: #E4C1AE;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 16px;
  padding: 16px 40px;
  border-radius: 32px;
  cursor: pointer;
}

@media (max-width: 1020px) {
  .box {
    flex-wrap: wrap;
    gap: 24px;
  }
  .box .box__img {
    flex-basis: 100%;
  }
  .box .box__content {
    flex-basis: 100%;
  }
  .box .box__content .box__title {
    margin-bottom: 12px;
  }
  .box .box__content .box__text {
    margin-bottom: 12px;
  }
}
.modal__wrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 18, 18, 0.7333333333);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  display: none;
}
.modal__wrapper .modal {
  background: white;
  padding: 46px;
  width: 920px;
  margin: 25px;
}
.modal__wrapper .modal .modal__close {
  cursor: pointer;
  margin: 0 0 0 auto;
  height: 22px;
  width: 22px;
}
.modal__wrapper .modal .modal__title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  margin: 0 0 24px 49px;
}
.modal__wrapper .modal .modal__body {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin: 0 0 10px 49px;
}
.modal__wrapper .modal .modal__body .modal__content {
  margin-bottom: 15px;
}
.modal__wrapper .modal .modal__body .modal__content span {
  font-weight: 700;
}
.modal__wrapper .modal .modal__body .count-reset {
  display: none;
  border: none;
  background-color: #e94c25;
  color: #FFFFFF;
  font-weight: 700;
  padding: 6px 10px;
  border-radius: 32px;
  cursor: pointer;
}

body {
  font-family: "Roboto Flex", sans-serif;
}